@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@1,700&family=Poppins:wght@300;400&display=swap");

/* font-family: 'Playfair Display', serif;
font-family: 'Poppins', sans-serif; */

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

/* navbar css */

.navbar {
  font-family: "Poppins", sans-serif;
  font-size: 17px;
  /* line-height: 26px; */
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  border-bottom: solid gray !important;
}

.nav-link {
  font-weight: 300 !important;
}

.active {
  border-bottom: 2px solid black;
}

.navbar-brand {
  font-weight: 700;
  font-family: "roboto", sans-serif;
  letter-spacing: 2px;
  display: flex;
  align-items: center;
  gap: 2px;
  padding-left: 3px;
}

.footer {
  color: white;
  margin-top: 20px;
  padding: 10px;
  background: #000000;
  background: linear-gradient(to right, #434343, #000000);
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: #ffefba;
  border-bottom: 1px solid #ffefba;
}

.pnf {
  display: flex;
  min-width: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.contactus {
  display: flex;
  justify-content: center;
  gap: 5px;
}

.register {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 70vh;
}
.form-group{
  margin-top: 10px;
  justify-content: flex-start;
}


.product-link{
  text-decoration: none !important;
  color: #000000 !important;
}